.card {
  display: flex;
  flex-direction: column;
  padding: 32px 16px 16px;
  border: 1px solid $color-light;
  box-sizing: border-box;
  border-radius: 5px;
  width: 200px;

  &__image {
    height: 134px;
    max-width: 100%;
    margin: 0 3px 40px;
  }

  &__title {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 4px;
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    margin-bottom: 16px;
    color: $color-secondary;
  }
}

.relevant {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;

  &__comments {
    padding-top: 1px;
    font-size: 10px;
    line-height: 14px;
    text-align: right;
  }
}

.price {
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;

  &__text {
    font-size: 12px;
    line-height: 18px;
    color: $color-secondary;
  }

  &__cost {
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
  }
}

.button {
  box-sizing: border-box;
  color: $color-white;
  border: none;
  background: $color-button;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  padding: 12px 0;
  cursor: pointer;

  &:hover {
    color: $color-button;
    background: $color-white;
    border: 1px solid $color-button;
  }
}
